<template>
  <div class="shop-info" ref="info">
    <div class="info-content">
      <section class="section">
        <h3 class="section-title">配送信息</h3>
        <div class="delivery">
          <div>
            <span class="delivery-icon">{{info.description}}</span>
            <span>由商家配送提供配送，约{{info.deliveryTime}}分钟送达，距离{{info.distance}}</span>
          </div>
          <div class="delivery-money">配送费￥{{info.deliveryPrice}}</div>
        </div>
      </section>
      <Split></Split>
      <section class="section">
        <h3 class="section-title">活动与服务</h3>
        <Active isScroll="No"/>
      </section>
      <Split></Split>

      <section class="section">
        <h3 class="section-title">商家实景</h3>
        <div class="pic-wrapper" ref="pics">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="(pic,index) in info.pics" :key="index">
              <img width="120" height="90" :src="pic">
            </li>
          </ul>
        </div>
      </section>
      <Split></Split>

      <section class="section">
        <h3 class="section-title">商家信息</h3>
        <ul class="detail">
          <li>
            <span class="bold">品类</span>
            <span>{{info.category}}</span>
          </li>
          <li>
            <span class="bold">商家电话</span>
            <span>{{info.phone}}</span>
          </li>
          <li>
            <span class="bold">地址</span>
            <span>{{info.address}}</span>
          </li>
          <li>
            <span class="bold">营业时间</span>
            <span>{{info.workTime}}</span>
          </li>
        </ul>
      </section>
    </div>
  </div>  
</template>

<script>
import BScroll from 'better-scroll'
import Active from '../../../components/Actives/Active'
import {mapState} from 'vuex'
export default {

  mounted() {//在初始显示之后才执行
      if(!this.info.pics) {
        return
      }
      this._initScroll()
  },
  components:{
    Active
  },
  methods: {
    // 初始化创建用于滚动的scroll对象
    _initScroll () {
      // 创建用于垂直滑动的scroll对象
      new BScroll('.shop-info', {
        click: true
      })
      // 动态计算指定ul的样式宽度
      const ul = this.$refs.picList
      const liWidth = 120
      const space = 6
      const count = this.info.pics.length
      const width = (liWidth + space) * count - space
      ul.style.width = width + 'px'
      // 创建用于水平滑动的scroll对象
      new BScroll('.pic-wrapper', {
        click: true,
        scrollX: true, // 水平滑动
      })
    }
  },
  computed:{
    ...mapState(['info'])
  },
  watch: {
    info (value) { // info更新了
      this.$nextTick(() => {
        this._initScroll()
      })
    }
  } 
}  
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import '../../../assets/stylus/minxins'
  .shop-info
    position: absolute
    top: 285px
    bottom: 0
    left: 0
    width: 100%
    background: #fff;
    overflow: hidden
    .section
      padding 16px 14px 14px
      font-size 16px
      background-color #fff
      color #666
      border-bottom 1px solid #eee
      position relative
      .section-title
        color #000
        font-weight 700
        line-height 16px
        margin-bottom 12px
        > .iconfont
          float right
          color #ccc
      .delivery
        margin-top 16px
        font-size 13px
        line-height 18px
        .delivery-icon
          width 55px
          font-size 11px
          margin-right 10px
          display inline-block
          text-align center
          color #fff
          background-color #0097ff
          padding 1px 0
          border-radius 4px
        .delivery-money
          margin-top 5px


      .pic-wrapper
        width: 100%
        overflow: hidden
        white-space: nowrap
        margin-top 16px
        .pic-list
          font-size: 0
          .pic-item
            display: inline-block
            margin-right: 6px
            width: 120px
            height: 90px
            &:last-child
              margin: 0
      .detail
        margin-bottom -16px
        > li
          display flex
          justify-content space-between
          align-items center
          margin-right -10px
          padding 16px 12px 16px 0
          line-height 16px
          bottom-border-1px(#ddd)
          font-size 13px
          > .bold
            font-weight 700
            color #333
          &:last-child
            border-none()  
</style>



